*{
    padding: 0;
    margin: 0;
    border: 0;
}

*,*:before, *:after{
	box-sizing: border-box;
}

:focus, :active{outline: none;}
a:focus, a:active{outline: none;}

nav, footer, header, aside{display: block;}

html, body{
height:100%;
width:100%;
font-size :100%;
line-height: 1;
font-size:14px;
-ms-text-size-adjust: 100%;
-moz-text-size-adjust: 100%;
-webkit-text-size-adjust: 100%;
}

input, button, textarea{font-family: inherit;
} 

input::-ms-clear{display: none;}
button{cursor: pointer;} 
button::-moz-focus-inner{padding: 0; border:0;} 
a, a:visited{text-decoration: none; color: black;}
a:hover{text-decoration: none;}
ul li{list-style: none;} 
img{vertical-align: top;}


h1,h2,h3,h4,h5,h6{font-size: inherit; font-weight: 400;}

@font-face {
    font-family: 'Roboto';
    src: url(/static/media/Roboto.c613ca81.ttf);
}

@font-face {
    font-family: 'Roboto';
    src: url(/static/media/Robotobold.1fc6a83d.ttf);
    font-weight: bold;
}

body {
    font-family: 'Roboto';
    min-width: 300px;
}

h2 {
    font-size: 50px;
    font-weight: bold;
}

.logo{
    width:auto;
    height: 50px;
}
.beginner-screen {
    height: 700px;
    width: 100%;
    background: url(/static/media/background.91a5c93a.png) no-repeat;
}

.beginner-screen_information {
    margin: 30px 0px 0px 60px;
}

.beginner-screen h1 {
    font-size: 80px;
    font-weight: bold;
}

.beginner-screen_information-text {
    width: 600px;
    font-size: 14px;
    line-height: 18px;
    padding: 15px 80px 0px 0px;
    margin-bottom: 20px;
}

.beginner-screen_information_button-container {
    padding-top: 20px;
}

.beginner-screen a {
    padding: 20px 40px 20px 40px;
    background: #000;
    color: #fff;
    font-size: 18px;
    transition: 1s all;
}

.beginner-screen a:hover {
    background: #fff;
    color: #000;
    border: 1px solid #000;
}

.beginner-screen a:visited {
    color: #fff;
}

@media screen and (max-width:600px) {
    .beginner-screen h1 {
        font-size: 50px;
        text-align: center;
    }

    .beginner-screen_information {
        margin: auto;
    }

    .beginner-screen_information-text {
        width: 100%;
        padding-left: 50px;
    }

    .beginner-screen_information_button-container {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
}

@media screen and (max-width:350px) {
    .beginner-screen h1 {
        font-size: 30px;
    }

    .beginner-screen a {
        padding: 15px 20px 15px 20px;
        font-size: 14px;
    }
}

.beginner-screen_information span {
    margin-top: 50px;
}
.car-card-item {
    width: 300px;
    height: auto;
    margin: 10px 20px 20px 20px;
}

.car-card-item img{
    width: 100%;
    height: auto;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: 0.5s all;
}

.car-card-item img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.car-card-item h3{
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}

.car-card-item_stats{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.car-card-item_stats div{
    text-align: center;
    padding-bottom: 20px;
}

.car-card-item_stats div img{
    width: 40px;
    height: auto;
}

.car-card-item_stats div h6{
    margin-top: 10px;
    font-size: 16px;
    font-weight: bold;
}

.car-card-item_stats div span{
    font-size: 14px;
}

.car-card-item a{
    display: block;
    width: 100%;
    background: #000;
    text-align: center;
    color: #fff;
    padding: 15px 0px 15px 0px;
    transition: 1s all;
}

.car-card-item a:visited{
    color: #fff;
}

.car-card-item a:hover{
    background: #fff;
    color: #000;
    border: 1px solid #000;
}
.cars-assortiment {
    width: 100%;
    height: auto;
    padding: 0px 50px 0px 50px;
}

.cars-assortiment-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
.reserve-form_text{
    padding:20px 0px 0px 50px;
}

.reserve-form_text h2{
    margin-bottom: 10px;
}


.reserve-form_container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0px 20px 0px;
}

.reserve-form form{
    display: grid;
    width: 300px;
}

.reserve-form form input{
    padding: 20px 10px 20px 20px;
    margin: 10px 0px 10px 0px;
    border: 1px solid #999;
}

.reserve-form form input:last-child{
    background-color: #000;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    font-size: 20px;
    padding: 20px 10px 20px 0px;
    border: none;
    transition: 1s all;
}

.reserve-form form input:last-child:hover{
    background-color: #fff;
    color: #000;
    border: 1px solid #000;
}

.reserve-form_img{
    width: 70%;
    height: auto;
    margin: auto;
}

.reserve-form_img img{
    width: 100%;
    height: auto;
}

.reserve-form form input:last-child:disabled{
    cursor: not-allowed;
    color: #999;
    background-color: #343434;
}

header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    height: auto;
    padding: 10px 0px 10px 0px;
    border-bottom: 1px solid #E5E5E5
}

header div {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: auto;
}

header div:first-child {
    margin-left: 100px;
}

@media screen and (max-width:685px) {
    header div:first-child {
        margin-left: 0px;
    }
}

header ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

header ul li {
    text-align: center;
    padding: 10px 40px 10px 20px;
}

.header-menu_item {
    font-size: 14px;
    text-decoration: none;
    transition: 2s all;
}

.header-menu_item:hover {
    color: black;
    text-decoration: underline;
}
footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 0px 10px 0px;
    border-top: 1px solid #E5E5E5;
}

footer div {
    height: 100%;
}

footer div span {
    text-align: center;
}
